export const fetchRefInfo = async (account: string) => {
  const calls = vaultsConfig.map((farm) => {
    return {
      address: farm.stakingContract,
      name: 'getRefInfo',
      params: [account],
    }
  })

  const rawInfo = await multicall(masterchefABI, calls)

  const parsedRefInfo = rawInfo.map((info) => {
    return {
      referrer: info[0],
      referEarnings: new BigNumber(info[1]._hex).toJSON(),
      referCounts: new BigNumber(info[2]._hex).toJSON(),
    }
  })
  return parsedRefInfo
}
